import React, { Component } from 'react';
import '../index.css';


class App extends Component {
    constructor() {
        super();
        this.state = { Questions:[
                {
                    title: "a hard interview question",
                    content:"k Sum",
                    tags: ["sql", "probability", "modelling"],
                    companies: ["facebook", "linkedin", "netflix"],
                    positions: ["Business Analyst", "data scientist"],

                },
               {
                    title: "easy question to solve",
                    content:"A + B Problem",
                    tags: ["sql", "probability", "statistics"],
                    companies: ["linkedin", "amazon", "google"],
                    positions: ["software engineer", "marketing Analyst", "ML Engineer"],

                },
                {
                    title: "a hard question to solve",
                    content:"Red and Black Chess",
                    tags: ["sql", "probability", "statistics"],
                    companies: ["facebook", "linkedin", "amazon", "google"],
                    positions: ["software engineer", "marketing Analyst", "data scientist", "ML Engineer"],

                },
                {
                    title: "easy question to solve",
                    content:"Fizz Buzz",
                    tags: ["sql", "probability", "statistics"],
                    companies: ["linkedin", "amazon", "google"],
                    positions: ["data scientist", "ML Engineer"],

                }
            ],

        filterQuestions: []
        }
    }

    componentDidMount(){
        this.setState({
            filterQuestions: this.state.Questions
        })
    }

    //Filter title
    thandleClick = event => {
        const byOrigin = event.target.value
        let filterQuestions = []
        filterQuestions = this.state.Questions.filter(question => question.title.includes(byOrigin))
        this.setState({filterQuestions: filterQuestions})

    }

    //Filter companies
    chandleClick = event => {
        const byOrigin = event.target.value
        let filterQuestions = []
        filterQuestions = this.state.Questions.filter(question => question.companies.includes(byOrigin))

        this.setState({filterQuestions: filterQuestions})
      }

      //Filter position
    phandleClick = event => {
        const byOrigin = event.target.value
        let filterQuestions = []
        filterQuestions = this.state.Questions.filter(question => question.positions.includes(byOrigin))

        this.setState({filterQuestions: filterQuestions})
    }

      //Reset button
      resetClick = event =>{
          this.setState({
              filterQuestions: this.state.Questions
          })
      }


    render() {

        const renderAll = this.state.filterQuestions.map(question =>
            <div  key={question.content}>{question.content}</div>
        )
        return (
            <div className="container">
                <div className="main-content">
                    <button className='filter' value='facebook' onClick={this.chandleClick}>facebook</button>
                    <button className='filter' value='google' onClick={this.chandleClick}>google</button>
                    <button className='filter' value='software engineer' onClick={this.phandleClick}>software engineer</button>
                    <button className='filter' value='data scientist' onClick={this.phandleClick}>data scientist</button>
                    <button className='reset' value='all' onClick={this.resetClick}>reset</button>
                    <br />
                    <br />
                    <button className='hard' value='hard' onClick={this.thandleClick}>a hard question</button>
                    <button className='easy' value='easy'  onClick={this.thandleClick}>easy question to solve</button>

                    <br />
                    <h3>Questions: </h3>
                    <p>{renderAll}</p>

                    <p className='bottom'>Total Questions: {this.state.filterQuestions.length}</p>
                </div>
            </div>
        );
    }
}

export default App;